/* import files css */
@import url(./fonts.css);
@import url(./partials/_variables.css);
@import url(./partials/_global.css);


/* Navigation */

.header-area .main-menu .navbar .navbar-brand {
  padding: 0 2rem 0 5rem;
}

.header-area .main-menu .navbar{
  /* padding: 1rem 20rem; */
}

.header-area .main-menu .nav-item .nav-link{
  font: normal 500 15px/2 var(--roboto);
  text-transform: uppercase;
  padding: 1.7rem;
  color: var(--title-color);
}
.header-area .main-menu .navbar-nav .active a{
  background: var(--gradiente-color);
  -webkit-background-clip:text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.header-area .main-menu .navbar-nav a:hover{
  background: var(--gradiente-color);
  -webkit-background-clip:text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
/* Navigation */

/* Banner Area */

.site-main .site-banner{
  background: url(../img/home-banner.png) no-repeat 5% 50%;
}
.site-main .site-banner .site-title h3{
  padding-top: 35%;
  font-weight: 500;
  font-size:2.5rem;
}
.site-main .site-banner .site-title h1{
  font-size: 5rem;
}
.site-main .site-banner .site-title h4{
  font-size: 1.5rem;
}
.site-main .site-banner .banner-image > img{
  padding: 7rem 0;
  width: 90rem;
}

/* Banner Area */

/* About Area */
.site-main .about-area{
  padding: 8rem;
}
.site-main .about-area h2 > span{
  display: block;
  font: normal bold 48px/60px var(--roboto);
  letter-spacing: 15px;
}
.site-main .about-area .paragraph > p{
  padding: .4rem 0;
}
/* About Area */

/* Brand Area */

.site-main .brand-area{
    padding: 1rem 15rem;
    margin-bottom: 4rem;
}

.site-main .brand-area .first-row .col-lg-4{
    display: flex;
    justify-content: center;
}

.site-main .brand-area .first-row .col-lg-4 > .single-brand{
    border: 1px solid var(--border-color);
    text-align: center;
    padding: 1.8rem 0rem;
    margin: .9rem 0;
    width: 180px;
    height: 100px;
    cursor: pointer;
    transition: transform .4s ease;
}

.site-main .brand-area .first-row .col-lg-4 > .single-brand > img{
    width: 50%;
}

.site-main .brand-area .first-row .col-lg-4 > .single-brand:hover{
    box-shadow: var(--box-shadow);
    transform: scale(1.1);
}

.site-main .brand-area .first-row .col-lg-4 > .single-brand:hover > img{
    filter: brightness(0.15);
}

.site-main .brand-area .experience-area{
    padding-left: 1rem;
}

.site-main .brand-area .experience-area span{
    display: block;
}

.site-main .brand-area .experience-area .years-area{
    background: url(../img/client-info-bg.png) no-repeat 45px 20px;
    padding: 2rem 0;
    width: 40rem;
    height: 18rem;
    padding-left: 8rem;
    position: relative;
}

.site-main .brand-area .experience-area .bg-panel{
    position: absolute;
    left: -190px; top: 0%;
    background: #e029290a;
    width: 400px;
    height: 400px;
    margin-left: 340px;
    z-index: -1;
}

.site-main .brand-area .experience-area .years-area .years{
    font: normal bold 84px/40px var(--roboto);
    color: var(--primary-color-2);
    z-index: 2;
}

.site-main .brand-area .experience-area .years-area h2 > span{
    font: normal 600 22px/30px var(--roboto);
}

.site-main .brand-area .experience-area .call-area{
    padding-left: 10rem;
}
/* Brand Area */

/* Services area */

.site-main .services-area{
  padding: 7rem 2rem;
}
.site-main .services-area .services-title{
  padding: 0 14rem;
}
.site-main .services-area .services-title h1{
  font-size:35px;
}
.site-main > .services-area .services{
  background: rgba(216, 226, 230, 0.52);
  /* background:#e5e9ea; */
  padding: 2rem 0;
  width: 16rem;
  border-radius: 25px;
  margin: 5rem .5rem;
  transition: box-shadow .7s ease;
}
.site-main > .services-area .services:hover{
  box-shadow: var(--box-shadow);
}
/* Services area */

/* Project Area */

.site-main .project-area{
  padding: 4rem 0;
}
.site-main .project-area .button-group button{
  background: transparent;
  border: none;
  /* cursor:pointer; */
  font: normal 500 16px/130px var(--roboto); /* style, weight, size, height, family*/
  text-transform: uppercase;
}
.site-main .project-area .button-group button + button {
  padding-left:3rem;
  /* a partir del primer botton */
}
.site-main .project-area .grid .our-project > .title h4{
  font: normal 700 25px/12px var(--roboto);
}

/* Project Area */
